import React from 'react'
import { AiFillPrinter } from 'react-icons/ai'
import { buttonActionSolid } from './Styles/Styles'

const PrintButton = () => {
  return (
    <>
    <div className='fixed bottom-10 text-center  justify-center items-center  w-screen'>
                    <button onClick={() => window.print()} className={buttonActionSolid}>
                        <AiFillPrinter className='inline text-lg' />
                        print
                    </button>
                </div>
    </>
  )
}

export default PrintButton